<template>
<div id="home">
    <NavBar>
    <template v-slot:default>å¾ä¹¦åå</template>
    </NavBar>
    <TabControl v-show="isTabFixed" @tabClick="tabClick" :titles="['çé','æ°ä¹¦','ç²¾é']"></TabControl>
    <div class="wrapper">
        <div class="content">
            <div ref="banref">
                <!--<div class="banners">
                    <img src="~assets/images/feet.jpg" alt="">
                </div>-->
              <home-swiper  :banners="banners"></home-swiper>
                <RecommendView :recommends="recommends"></RecommendView>
            </div>
            <TabControl @tabClick="tabClick" :titles="['çé','æ°ä¹¦','ç²¾é']"></TabControl>
            <GoodsList :goods="showGoods"></GoodsList>
        </div>
        <BackTop @bTop="bTop"  v-show="isShowBackTop"></BackTop>
    </div>
    
   

</div>
</template>
<script>
import NavBar from 'components/common/navbar/NavBar.vue';
import RecommendView from './ChildComps/RecommendView.vue';
import TabControl from "components/content/tabControl/TabControl.vue";
import GoodsList from "components/content/goods/GoodsList.vue";
import BackTop from 'components/common/backtop/BackTop.vue';
import HomeSwiper from "./ChildComps/HomeSwiper";
import { ref,reactive,onMounted,computed ,watchEffect ,nextTick} from "vue";
import { getHomeAllData , getHomeGoods } from "network/home.js";
import BScroll from 'better-scroll';

export default {
    name:"Home",
    setup(){
        let isTabFixed = ref(false);
        let isShowBackTop = ref(false);        
        //ä¸´æ¶åé const temid = ref(0);
        let banref = ref(null);
        const recommends = ref([]);
        //åååè¡¨æ°æ®æ¨¡å
        const goods = reactive({
            sales:{page:1,list:[]},
            new:{page:1,list:[]},
            recommend:{page:1,list:[]}
        });
        const currentType = ref('sales');
        const showGoods = computed(()=>{
            return goods[currentType.value].list
        })

        let bscroll = reactive({});
        let  banners = ref([]);
        onMounted(()=>{
            getHomeAllData().then(res=>{
                //console.log(res.goods);
                recommends.value = res.goods.data;
                banners.value = res.slides;
            })

            getHomeGoods('sales').then(res=>{
                goods.sales.list = res.goods.data;
            })
            getHomeGoods('new').then(res=>{
                goods.new.list = res.goods.data;
            })
            getHomeGoods('recommend').then(res=>{
                goods.recommend.list = res.goods.data;
            })
            //åå»ºBetterScrollå¯¹è±¡
            bscroll = new BScroll(document.querySelector('.wrapper'),{
                prodeType:3,
                click:true,//æ¯å¦åè®¸ç¹å»
                pullUpLoad:true//ä¸æå è½½æ´å¤é»è®¤æ¯false
            });
            //è§¦åæ»å¨äºä»¶
            bscroll.on('scroll',(position)=>{
                //console.log(position);
                isShowBackTop.value =isTabFixed.value = (-position.y) > banref.value.offsetHeight;
                //console.log(isShowBackTop);
            })
            //ä¸æå è½½æ°æ®ï¼è§¦åpullingup
            bscroll.on('pullingUp',()=>{
                const page = goods[currentType.value].page +1;
                getHomeGoods(currentType.value,page).then((res)=>{
                    //æ­¤å¤éè¦å°è·åå°çæ°æ®éè¿æ©å±è¿ç®ç¬¦ç»æåèµå¼
                    goods[currentType.value].list.push(...res.goods.data);
                    goods[currentType.value].page += 1;
                })
                //å®æä¸å¦ï¼ç­æ°æ®è¯·æ±å®æï¼æ´æ°æ°æ®å±ç¤ºåºæ¥
                bscroll.finishPullUp();
                //éæ°è®¡ç®é«åº¦
                bscroll.refresh();
                console.log('å½åç±»åï¼'+ currentType.value+'å½åé¡µæ°ï¼'+page);
            })
            
        })
        


        const tabClick = (index)=>{
            
            let types = ['sales','new','recommend'];
            currentType.value = types[index];
            //åæ¢é¡µé¢åéæ°è®¡ç®é«åº¦
            nextTick(()=>{
                bscroll && bscroll.refresh();
            })
        }
        //çå¬ä»»ä½ä¸ä¸ªåé
        watchEffect(()=>{
            nextTick(()=>{
                bscroll && bscroll.refresh();
            })
        });

        const bTop = ()=>{
            bscroll.scrollTo(0,0,1000);
        }
        return {
            recommends,
            tabClick,
            goods,
            showGoods,
            isTabFixed,
            banref,
            isShowBackTop,
            bTop,
            banners
        }
    },
    components:{
        NavBar,
        RecommendView,
        TabControl,
        GoodsList,
        BackTop,
        HomeSwiper
    }
}
</script>
<style lang="scss" scoped>
#home {
    height: 100vh;
    position: relative;
}
.banners {
    width: 100%;
    background-color: rgb(136, 127, 255);
    height: 200px;
    img {
    
        width: 100%;
        height: 100%;
        margin: 0px;
    }
}
.wrapper{
    position: absolute;
    top: 40px;
    bottom: 50px;
    left: 0px;
    right: 0px;
    overflow: hidden;
    
}
</style>